.nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-between;
  padding: 0.4rem 5rem 0rem 5rem;
  align-items: center;
  button {
    padding: 0.7rem 1.2rem;
    font-size: 1rem;
    cursor: pointer;
    transition: 0.2s;
    width: auto;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);

    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    z-index: 1;
    &:hover {
      background-color: #2c2a2ae0;
      color: #fff;
      transition: 0.2s;
    }
  }
}
@media (max-width: 600px) {
  .nav {
    padding: 0.5rem 1rem;
    button {
      backdrop-filter: blur(0);
      background-color: #6d6c6cfb;
    }
  }
}
