.player-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.time-controls {
  width: 50%;
  display: flex;
  input {
    width: 100%;
  }
  p {
    padding: 0.5rem;
  }
}
.play-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  width: 30%;
  svg {
    cursor: pointer;
  }
}
.icon {
  transition: 0.3s;
  font-size: 1.5rem;
}
.range-input {
  all: initial;
  background-color: black;
  height: 10px;
  margin-top: 0.8rem;
  border-radius: 50px;

  &:hover {
    color: black !important;
  }
}
.range-input::-webkit-slider-thumb {
  background-color: white !important;
}

@media screen and (max-width: 600px) {
  .time-controls {
    width: 80%;
  }
  .play-controls {
    width: 50%;
  }
}
