.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  background: #45423a4d;
  box-shadow: 0 8px 32px 0 rgba(19, 20, 44, 0.37);
  backdrop-filter: blur(10px);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transform: translateX(-120%);
  opacity: 0.5;
  transition: 0.4s;
  h1 {
    color: rgb(3, 3, 3);
    padding: 2rem 1rem 0.5rem 1.7rem;
  }
}
.library-songs {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 25%;
    object-fit: contain;
  }

  .library-song {
    display: flex;
    padding: 1rem;
    cursor: pointer;
    transition: 0.2s ease;
    &:hover {
      background: rgba(15, 13, 13, 0.424);
      color: #fff;
      transition: all 0.3s ease-in;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
    }

    .song-description {
      padding: 0.5rem;
      font-family: "Josefin Sans";
      letter-spacing: 0.1em;
      padding-left: 1rem;
      line-height: 1.2rem;
      h3 {
        font-size: 1rem;
      }
      h4 {
        font-size: 0.7rem;
        padding: 0;
      }
    }
  }
}
.selected {
  color: rgb(255, 255, 255);
  background: rgba(26, 25, 25, 0.781);
  box-shadow: 0 8px 32px 0 rgba(9, 10, 25, 0.37);
  backdrop-filter: blur(15.5px);
  -webkit-backdrop-filter: blur(15.5px);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.active-library {
  transition: 0.4s ease-out;
  transform: translateX(0);
  opacity: 1;
}
@media (max-width: 600px) {
  .library {
    backdrop-filter: blur(0);
    background-color: #45423a !important;
  }
  .selected {
    backdrop-filter: blur(0);
  }
}
