@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400&family=Jost:wght@200;400&family=Montserrat:wght@200&family=Nunito:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  scrollbar-width: thin;
  letter-spacing: 0.1rem;
  scrollbar-color: #5e99ff transparent;
  box-sizing: border-box;
}
*::-webkit-scrollbar {
  width: 2px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}
*::-webkit-scrollbar-thumb {
  background: #5e99ff;

  border-radius: 50px;
  border: 0;
}
@import "./player";
@import "./songs";
@import "./library";
@import "./nav";
h1 {
  font-family: "Josefin Sans", sans-serif;
}
.app {
  height: 100vh;
}
p,
h2 {
  font-family: "Jost", sans-serif;
}
.music-section {
  transition: 0.5s;
  overflow-x: hidden;
}
.contents {
  padding-left: 10rem;
  transition: all 0.3s ease;
}
@media (max-width: 600px) {
  .contents {
    padding-left: 4rem;
    transition: 0.4s;
  }
}
