.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: rgb(0, 0, 0);

  .playing {
    animation-play-state: running;
  }
  .paused {
    animation-play-state: paused;
  }

  h1,
  h2 {
    font-size: 1.5rem;
    margin-top: 0.5rem;
    padding: 0.3rem;
  }
}
@keyframes imagerotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.cover-container {
  width: 20%;
  img {
    width: 100%;
    border-radius: 50%;
    margin: 0.5rem;
    object-fit: contain;
    animation: imagerotation 15s linear infinite;
    box-shadow: 0px 0px 56px #00000044, -0px -0px 56px #00000041;
  }
}
@media screen and (max-width: 600px) {
  .cover-container {
    width: 40%;
  }
}
